export var CHANGE_SCHEMA = 'CHANGE_SCHEMA';
export function changeSchema(introspection, displayOptions) {
    return {
        type: CHANGE_SCHEMA,
        payload: {
            introspection: introspection,
            displayOptions: displayOptions
        }
    };
}



